.finalContainer{
  display: flex;
  z-index: 2;
  border-radius: 24px;
  border: 2px solid #008C95;
  background: #FFF;
  justify-content: space-between;
  align-items: flex-end;
  gap: 16px;
  //padding-bottom: 100px;
  flex-direction: column;
  margin-top: 24px;
  button{
    cursor: pointer;
  }
  .wrap{
    padding: 16px;
    border-radius: 24px;
    align-items: center;
    display: flex;
    flex-direction: column;
    gap: 24px;
    background: white;
    .title{
      color: #000;
      font-family: Nipigaz Basic;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 120%;
    }
    .job{
      color: #008C95;
      font-family: Nipigaz Basic;
      font-size: 16px;
      font-style: normal;
      font-weight: 700;
      line-height: normal;

      background: white;
      padding: 12px 32px;
      border-radius: 60px;
      border: 2px solid #008C95;
      width: max-content;
    }
    input{
      border: inherit;
      border-bottom: 1px solid #E7E7E7;
      width: 100%;
      outline:none;
      font-feature-settings: 'clig' off, 'liga' off;
      font-family: Nipigaz Basic;
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      line-height: 24px; /* 133.333% */
      letter-spacing: 0.5px;
    }
    .acceptedBody {
      display: flex;
      gap: 10px;
      .check {
        cursor: pointer;
        min-width: 14px;
        max-width: 14px;
        min-height: 14px;
        max-height: 14px;
        border-radius: 100px;
        border: 1px solid #008C95;

        &.accepted {
          background: #008C95;
        }
      }
      .text{
        color: var(--project-text-secondary-text, #757575);
        font-feature-settings: 'clig' off, 'liga' off;
        font-family: Nipigaz Basic;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 18px; /* 112.5% */
        letter-spacing: -0.078px;
        a{
          color: #757575;
          text-decoration: underline;
          cursor:pointer;
        }
      }
    }
    .next{
      color: #FFF;
      font-family: Nipigaz Basic;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;

      padding: 12px 32px;
      width: max-content;
      border-radius: 60px;
      background: #008C95;
      border: inherit;
      min-width: 252px;
      @media screen and (max-width: 320px){
        min-width: 240px;
      }
      &.disableForm{
        background: #D0D0D0;
      }
    }
  }
}
