.mainContainer{
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-evenly;
  align-items: center;
  .wrapText {
    display: flex;
    flex-direction: column;
    gap: 16px;
    .mainText {
      color: #008C95;
      font-family: Nipigaz Basic;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 120%; /* 19.2px */
      padding: 12px;
      border-radius: 24px;
      border: 2px solid #008C95;
      background: #FFF;
    }

    .secondText {
      padding: 0 12px;
      color: #FFF;
      font-family: Nipigaz Basic;
      //overflow-x: hidden;
      font-size: 16px;
      font-style: normal;
      font-weight: 700;
      line-height: 120%; /* 19.2px */
      position: relative;

      &:before {
        content: url("../../img/arrow.svg");
        z-index: 1000;
        height: 100px;
        position: absolute;
        bottom: -117px;
        width: calc(100% - 32px);
        overflow: hidden;
      }
    }
  }
  button{
    border: inherit;
    color: #37808F;
    cursor: pointer;
    text-align: center;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Nipigaz Basic;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    border-radius: 65px;
    background: #FFF;
    width: 235px;
    padding: 20px 16px;
  }
}
