.GameContainer{
  display: flex;
  flex-direction: column;
  padding: 0 24px;
  height: 100vh; /* фолбэк для браузеров, которые не поддерживают пользовательские свойства */
  height: calc(var(--vh, 1vh) * 100);
  @media screen and (min-width: 988px) {
    height: 100%;
  }
  #gameWrap{
    position: relative;
    z-index: 2;
    width: 100vw;
    height: 100%;
  }
  .imgOpen{
    transform: scale(1);
    transition: transform 1s ease-out;
    //transform: scale(0);
  }
  .imgClose{
    z-index: 1;
    position: relative;
    transform: scale(0);
    transition: transform 1s ease-out;
    display: none;
  }
  .wrapPontEnd{
    opacity: 0;
  }
}

