.GameContainer{
  display: flex;
  flex-direction: column;
  padding: 0 24px;
  height: 100vh; /* фолбэк для браузеров, которые не поддерживают пользовательские свойства */
  height: calc(var(--vh, 1vh) * 100);
  @media screen and (min-width: 988px) {
    height: 100%;
  }
  .point{
    user-select:none;
    width: 12px;
    height: 12px;
    background: #FFF;
    border-radius: 50%;
    position: relative;

    cursor: pointer;
    .id{
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      margin: auto;
      color: #008C95;
      text-align: center;
      font-family: Inter;
      font-size: 8px;
      font-style: normal;
      font-weight: 600;
      line-height: normal;
      padding-top: 1px;

    }
  }
}
