.modalBg {
  display: none;
  //justify-content: center;
  //align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  max-width: 450px;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.20);
  z-index: 100;
  right: 0;
  margin: 0 auto;

  &.modalShow {
    //display: flex;
    display: initial;
  }

  .positionWrap{
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    height: 100%;
    justify-content: center;
  }
  .modalWrap {
    align-items: center;
    height: min-content;
    border-radius: 24px;
    border: 2px solid #008C95;
    background: #FFF;
    padding: 16px 12px;
    //display: flex;
    margin: 0 24px;
    //flex-direction: column;
    //justify-content: center;
    //align-items: center;
    display: flex;
    flex-direction: column;
    gap: 16px;

    .modalText {
      word-wrap: break-word;
      color: #008C95;
      font-family: Nipigaz Basic;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 120%; /* 19.2px */
    }

    .buttonNext {
      padding: 16px 32px;
      border: inherit;
      border-radius: 65px;
      background: #008C95;
      color: #FFF;
      text-align: center;
      font-feature-settings: 'clig' off, 'liga' off;
      font-family: Nipigaz Basic;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }
  }
}
