@font-face {
  font-weight: 400; /* Соответствует значению normal */
  font-family: Nipigaz Basic;
  src: url("../public/fonts/NipigazBasic-Regular.ttf");
}

@font-face {
  font-weight: 700; /* Соответствует значению bold */
  font-family: Nipigaz Basic;
  src: url("../public/fonts/NipigazBasic-Bold.ttf");
}

@font-face {
  font-weight: 300;
  font-family: Nipigaz Basic;
  src: url("../public/fonts/NipigazBasic-Italic.ttf");
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
